<template>
	<div class="chat">
		<h1 class="user">用户:{{currentUser.username}}</h1>
		<!-- 聊天列表 -->
		<div class="chaylist">
			<chatitem-com v-for='item,index in chatList' :chatItem='item'>
				{{item.chatcontent}}
			</chatitem-com>
		</div>
		<chatinput-com :sendEvent='sendEvent'></chatinput-com>
	</div>
</template>

<script>
	import chatinputCom from "./chatinputcom.vue"
	import chatitemCom from "./chatltem.vue"
	export default{
		props:['currentUser'],
		components:{
			chatinputCom,
			chatitemCom
		},
		data(){
			return{
				chatList:[
					{
						user:{
							username:'xiaomin',
							headerimg:require('../assets/1.jpg')
						},
						chatcontent:'你好啊'
					}
				]
			}
		},
		methods:{
			sendEvent:function(value){
				console.log(value)
				this.chatList.push({
					user:{
						username:"小明",
						headerimg:require('../assets/1.jpg')
					},
					chatcontent:value
				})
			}
		}
	}
</script>

<style scoped="scoped">
.chat{
	width: 500px;
	height: 700px;
	border: 1px solid #2C3E50;
}

</style>
