<template>
	<div>
		<input type="text" @keydown.enter="send" v-model="inputContent">
		<button @click="send">发送</button>
	</div>
</template>

<script>
	export default{
		props:['sendEvent'],
		data(){
			return{
				inputContent:''
			}
		},
		methods:{
			send:function(){
				console.log(this.inputContent)
				this.sendEvent(this.inputContent)
				this.inputContent=''
				
			}
		},
	}
</script>

