<template>
	<div class="userlist">
		<h1>用户列表</h1>
	<ul>
		<li v-for="item,index in userlist" @click="chooseUser(index)">
			<img :src="item.headerimg" alt="">
			<h3>{{item.username}}</h3>
		</li>
	</ul>
	</div>
</template>

<script>
	export default{
		data(){
				return{
					
				}
		},
		props:['userlist'], //父组件通过 props 向下传递数据给子组件；子组件通过 events 给父组件发送消息。
		methods:{
			chooseUser:function(index){
				console.log(index)
				//选择用户的触发事件，发送给父元素，子组件可以使用 $emit 触发父组件的自定义事件
				this.$emit('chooseuser',index)
			}
		}
	}
</script>

<style scoped="scoped">
	.userlist{
		width: 300px;
		height: 700px;
		background-color: skyblue;
	}
	.userlist ul li{
		display: flex;
	}
	.userlist ul li img{
		width: 80px;
		height: 80px;
	}
</style>
