<template>
  <div id="app">
	   <h1>{{msg}}</h1>
	  <div class="content">
		  <chat-com :currentUser="currentUser"></chat-com>
		  <userlist-com :userlist='userlist' @chooseuser='toggleUser'></userlist-com>
	  </div>
  </div>
</template>

<script>
import chatCom from './components/chatcom'
import userlistCom from "./components/userlistcom"
export default{
	name:'app',//可有可无
	components:{
		chatCom,userlistCom
	},
	data(){
		return {
			msg:'聊天框',
			//使用到的数据
			userlist:[
				{	
					username:'小明',
					headerimg:require('./assets/1.jpg')	//require相当于url
				},
				{
					username:'小王',
					headerimg:require('./assets/2.jpg')
				},
				{
					username:'小赵',
					headerimg:require('./assets/3.jpg')
				},
				{
					username:'小前',
					headerimg:require('./assets/4.jpg')
				},
				{
					username:'小孙',
					headerimg:require('./assets/5.jpg')
				},
			],
			//默认第一个
			currentUser:{
					username:'小明',
					headerimg:require('./assets/1.jpg')	//require相当于url
			}
		}
	},
	methods:{
		toggleUser:function(index){
			this.currentUser=this.userlist[index]
		}
	},
}
</script>

<style scoped="scoped">
	#app{
		margin: 100px auto;
		text-align: center;
	}
	.content{
		display: flex;
		width: 800px;
		height: 700px;
		margin: 0 auto;
	}
</style>
